@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
@import url(https://fonts.googleapis.com/css?family=Nunito&display=swap);
.App.theme-light {
  font-family: "Avenir", "Arial";
  font-weight: 400;
  color: black;
  background-color: white; }
  .App.theme-light ul,
  .App.theme-light li {
    list-style: none; }
  .App.theme-light img {
    max-width: 100%; }
  .App.theme-light h1,
  .App.theme-light h2,
  .App.theme-light h3 {
    font-family: "Montserrat";
    font-weight: 700; }
  .App.theme-light .theme-button {
    background-color: transparent;
    border: none;
    float: right;
    height: 50px;
    width: 50px;
    float: right;
    display: inline;
    cursor: pointer;
    margin-right: 25px; }
  .App.theme-light button {
    cursor: pointer;
    height: 50px;
    padding: 15px 35px 16px;
    /* border-radius: 2px; */
    font-family: "Avenir", sans-serif;
    font-weight: 600;
    font-size: 16px;
    text-transform: uppercase;
    background-color: #f7323f;
    border: solid 2px #464e54;
    color: #464e54;
    background: none; }
    .App.theme-light button button:hover {
      background-color: #e62d35; }
  .App.theme-light div.logo {
    content: url("https://res.cloudinary.com/dxrvvjvpf/image/upload/v1560824922/paint-brush-heart-png.png");
    height: 50px;
    position: relative;
    top: 25px;
    left: 27px; }
  .App.theme-light .hero-wrapper {
    background-image: url("https://d2qwzu24wcp0pu.cloudfront.net/purecollection/landing/SS18/sweater%20buying%20guide/9421503f.24342_Pure_Sweater_guide-v3_aimee_02.png/1176x440.fit.24342_Pure_Sweater_guide-v3_aimee_02.png");
    background-size: cover;
    padding: 0;
    margin: 0;
    overflow: hidden; }
  .App.theme-light .hero-image-container {
    margin: 97px 0; }
  .App.theme-light .hero-details {
    float: left;
    width: 45%;
    margin-left: 60px;
    margin-top: 30px;
    margin: 30px 0 30px 60px;
    color: #3f3f49; }
  .App.theme-light .hero-button {
    margin: 10px 0; }
  .App.theme-light .hero-button button:first-of-type {
    margin-right: 10px;
    font-size: 1em;
    background: #e62d35;
    padding: 10px 10px;
    width: 40%;
    border-radius: 3px;
    color: white;
    border: none; }
  .App.theme-light .hero-button button:last-of-type {
    font-family: "Montserrat";
    font-size: 1em;
    background: #3f3f49;
    padding: 10px 10px;
    width: 40%;
    border-radius: 3px;
    color: white;
    border: none; }
  .App.theme-light .show-basket {
    width: 20%;
    /* box-shadow: 0 0 17px 5px rgba(39, 43, 55, 0.15); */
    box-shadow: 0 2px 55px rgba(0, 0, 0, 0.1);
    background: white;
    float: left; }
  .App.theme-light .remove-btn {
    border-radius: 100%;
    background: #e62d35;
    color: white;
    font-size: 1em;
    padding: 4px 8px 4px 8px;
    border: none; }
  .App.theme-light .add-btn {
    margin-top: 10px;
    width: 100%;
    height: 36px;
    border-radius: 2px;
    text-align: center;
    color: #fff;
    background: #077915;
    border: 0;
    font-weight: 400;
    padding: 0 16px;
    font-family: inherit;
    font-size: inherit;
    cursor: pointer; }
    .App.theme-light .add-btn:hover {
      background-color: #e62d35; }
  .App.theme-light .clearfix:after {
    display: table;
    content: "";
    clear: both; }
  .App.theme-light .product-wrapper {
    width: 95%;
    margin: 20px auto; }
  .App.theme-light .basket-header {
    text-align: left;
    padding: 9px;
    margin: 3px 14px; }
  .App.theme-light .cart-list {
    list-style: none;
    margin: 0;
    font-size: 14px;
    padding: 0 9px; }
  .App.theme-light .cart-list li.active {
    color: #e62d35; }
  .App.theme-light .cart-list li {
    padding: 16px; }
  .App.theme-light .product-container {
    width: 75%;
    float: left;
    margin-left: 2%; }
  @media screen and (max-width: 812px) {
    .App.theme-light .hero-details {
      width: 65%;
      float: none; }
    .App.theme-light .showlist_container {
      width: 68%; } }
  @media screen and (max-width: 615px) {
    .App.theme-light .show-basket {
      display: none; }
    .App.theme-light .showlist_container {
      width: 99%; } }
  .App.theme-light .Items {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    width: 90%;
    max-width: 1200px;
    margin: 0 auto; }
  .App.theme-light .Item {
    margin: 10px;
    border-radius: 4px;
    width: 225px;
    min-height: 230px;
    padding-right: 20px;
    padding-left: 20px;
    padding-bottom: 20px; }
    @media (max-width: 1050px) {
      .App.theme-light .Item {
        width: 48%; } }
    @media (max-width: 600px) {
      .App.theme-light .Item {
        width: 100%;
        margin-bottom: 10px; } }
  .App.theme-light .Item__title {
    text-align: center;
    font-size: 1em;
    font-weight: 700;
    color: #3f3f49;
    width: 90%;
    margin: 20px auto 0;
    text-transform: uppercase; }
  .App.theme-light .Item__price {
    text-align: center;
    color: #3f3f49;
    font-size: 0.9em;
    margin-top: 10px; }
  .App.theme-light .Image {
    width: 100%;
    height: auto;
    position: relative;
    z-index: 1; }
  .App.theme-light .mybuttonoverlap {
    position: absolute;
    z-index: 2;
    top: 150px;
    display: none;
    left: 25px;
    background-color: white; }
  .App.theme-light .Image:hover .mybuttonoverlap {
    display: block; }

